ible a la temperatura ambiente, debe hacerse en lugares con buena ventilación garantizando una buena higiene y salubridad para el producto terminado. ")])]),_c('div',{staticClass:"col-md-8 col-lg-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/c8.svg")}})])])])])],1),_c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"tarjeta color-secundario p-3 mb-5 px-5 fnd-3"},[_c('div',{staticClass:"row justify-content-around align-items-center"},[_vm._m(4),_c('div',{staticClass:"col"},[_c('div',{staticClass:"row justify-content-between align-items-center"},[_vm._m(5),_c('div',{staticClass:"col-sm-auto"},[_c('a',{staticClass:"boton color-acento-botones texto-blanco",attrs:{"href":('https://www.youtube.com/watch?v=yi866Z1roq8 '),"target":"_blank"}},[_c('span',[_vm._v("Enlace")]),_c('i',{staticClass:"fas fa-link"})])])])])])])])]),_c('separador'),_vm._m(6),_vm._m(7),_c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-10"},[_c('h3',{staticClass:"text-center mb-5"},[_vm._v("Conservación de la panela")]),_c('ImagenInfografica',{staticClass:"color-acento-botones",scopedSlots:_vm._u([{key:"imagen",fn:function(){return [_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img-12.svg")}})])]},proxy:true}])},[_c('div',{staticClass:"tarjeta color-secundario p-5 fnd-4",attrs:{"x":"9.7%","y":"8%","numero":"1"}},[_c('p',[_vm._v("Al terminar el proceso de enfriamiento, continua la etapa de empaquetamiento en un sitio limpio y seco, esto depende del destino al cual será enviado el producto, dado que se debe diferenciar la de consumo propio y la de distribución en el mercado local como tiendas y supermercados o si es de tipo exportación. ")])]),_c('div',{staticClass:"tarjeta color-secundario p-5 fnd-4",attrs:{"x":"29.7%","y":"93%","numero":"2"}},[_c('p',[_vm._v("Si es para consumo propio es almacenada bajo empa